import { followUser, unfollowUser } from "@/api/user";
import { addFavorite, deleteFavorite, deleteArticle } from "@/api/article";
import { mapState } from "vuex";
export default {
  name: "ArticleMeta",
  props: {
    article: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      followDisabled: false,
      favoriteDisabled: false,
    };
  },
  computed: {
    ...mapState(["user"]),
    isUser() {
      return this.user ? this.article.author.username === this.user.username : false;
    },
  },
  methods: {
    async onFollow() {
      const author = this.article.author;
      // 用户未登录
      if(!this.user) {
        this.$router.push('/login')
        return
      }
      // 用户已登录
      this.followDisabled = true;
      // 关注用户
      if (!author.following) {
        await followUser(author.username);
        author.following = true;
      }
      // 取消关注用户
      else {
        await unfollowUser(author.username);
        author.following = false;
      }
      this.followDisabled = false;
    },
    async onFavorite() {
      const article = this.article;
      // 用户未登录
      if(!this.user) {
        this.$router.push('/login')
        return
      }
      
      // 视图与网络交互功能 做网络延迟禁用处理
      this.favoriteDisabled = true;
      if (article.favorited) {
        // 取消点赞
        await deleteFavorite(article.slug);
        // 点赞高亮
        article.favorited = false;
        // 点赞总数
        article.favoritesCount -= 1;
      } else {
        // 添加点赞
        await addFavorite(article.slug);
        article.favorited = true;
        article.favoritesCount += 1;
      }
      this.favoriteDisabled = false;
    },
    async delArticle () {
      await deleteArticle(this.article.slug)
      this.$router.push('/')
    }
  },
};
