import { getComment, addComment, deleteComment } from "@/api/article";
import { mapState } from "vuex";
export default {
  name: "ActicleComment",
  props: {
    article: {
      type: Object,
      required: true,
    },
  },
  computed: {
    ...mapState(["user"]),
  },
  data() {
    return {
      // 文章列表
      comments: [],
      // 记录评论输入文本
      comment: {
        body: "",
      },
      // 评论按钮禁用
      postDisabled: false,
    };
  },
  // 不需要SEO
  async mounted() {
    const { data } = await getComment(this.article.slug);
    this.comments = data.comments;
  },
  methods: {
    // 添加评论
    async onSubmit() {
      if (this.comment.body) {
        this.postDisabled = true;
        const { data } = await addComment(this.article.slug, {
          comment: this.comment,
        });
        this.comments.unshift(data.comment);
        this.comment.body = "";
        this.postDisabled = false;
      }
    },
    // 删除评论
    async onDelete(id) {
      const arr = this.comments;
      await deleteComment(this.article.slug, id);
      arr.splice(
        arr.findIndex((comment) => comment.id === id),
        1
      );
    },
  },
};
